import React from 'react'
import {Route, Switch} from 'react-router-dom'
import Home from './home/Home'
import Login from './registration/Login.js'
import Register from './registration/Register.js'
import Exposition from './exposition/Exposition.js'
import Dashboard from './Dashboard/Dashboard.js'
import Details from './images/ImageDetails.js'
import ExpoDetails from './exposition/ExpoDetails'

const Main = () => (
    <main>
        <Switch>
            <Route exact path='/' component={Home} />
            <Route exact path='/login' component={Login} />
            <Route exact path='/register' component={Register} />
            <Route exact path='/dashboard' component={Dashboard} />
            <Route exact path='/image/details/:id' component={Details} />
            <Route exact path='/exposition/details' component={ExpoDetails} />
            <Route exact path='/exposition/details/:id' component={ExpoDetails} />
            <Route exact path='/exposition/view/:id' component={Exposition} />
            <Route exact path='/exposition/edit/:id' component={Exposition} />
        </Switch>
    </main>
);

export default Main;