import React from 'react'
import ReactDOM from 'react-dom'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'
import styles from '../../styles/exposition.module.css'

import ThreeContainer from '../ThreeJS/ThreeContainer.js'
import Header from '../Header'



export default class Exposition extends React.Component {
    constructor(params){
        super(params);

        this.chooseImage = this.chooseImage.bind(this);
        this.saveExpo = this.saveExpo.bind(this);
        this.state = {images: []};
    }
    componentDidMount(){
        requester.get('/api/images')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    this.setState({
                        images: res.images,
                        ids: res.ids
                    });
                }
                else
                    observer.fire('notifications', res);
            });
        requester.get('/api/expositions/details/' + this.props.match.params.id)
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    this.setState({details: res.data});
                }
            })
    }
    componentWillUnmount(){
        
    }

    chooseImage(e) {
        //this.setState({top: e.clientY - e.target.offsetTop+5});
        //this.setState({left: e.clientX - e.target.offsetLeft+5});
    }

    saveExpo(frameId, imgSrc, imgId, position, rotation) {
        position = position.toArray();
        rotation = rotation.toArray();
        requester.post({frameId, imgSrc, imgId, matrix: {position, rotation}}, '/api/expositions/edit/' + this.props.match.params.id);
    }

    dragStart(e) {
        console.log(e.target.id);
        e.dataTransfer.setData("text/id", e.target.id);
    }
    
    render(){

        return (
            <div onMouseMove={this.chooseImage}>
                <Header></Header>
                <ThreeContainer chooseImage={this.chooseImage} saveExpo={this.saveExpo} details={this.state.details}></ThreeContainer>
                <div className={styles.menu}>
                {
                    this.state.images.map((img, i) => 
                    <div className={styles.details} key={i}>
                        <img src={'http://res.cloudinary.com/dvn7y9omc/image/upload/' + img.src} className={styles.img} draggable='true' onDragStart={this.dragStart} id={this.state.ids[i]}></img>
                        {img.title}
                    </div>)
                }
                </div>
            </div>
        )
    }
}