import React from 'react'
import ReactDOM from 'react-dom'
import '../styles/header.css'
import logo from '../resources/logo2.png'
import profile from '../resources/profile.png'
import withLogging from '../hocs/withLogging.js'
import Menu from './Menu'
import {Link} from 'react-router-dom'
import requester from '../helpers/requester';

class Header extends React.Component {
    
    constructor(props) {
        super(props);
        
        this.state = {showHeader: props.transparent, text: '', index: 0};
        this.showHeader = this.showHeader.bind(this);
        this.logout = this.logout.bind(this);
        this.lastScroll = 0;

    }
    componentDidMount() {
        window.addEventListener('scroll', this.showHeader);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.showHeader);
    }

    showHeader(e) {
        let scroll = window.pageYOffset;
        if(scroll >= 10) this.setState({showHeader: true});
        else if(!this.props.transparent) this.setState({showHeader: false});
    }

    logout() {
        requester.get('/logout');
    }
    
    render() {
        let showHeader = this.state.showHeader ? '':'nav-up';

        if(!this.props.logging){
            return (
                <div>
                    <header className={showHeader}>
                    <img src={logo} id='logo'></img>
                    <Menu></Menu>
                    <input type="text" placeholder="Search.."></input>
                    <button type="submit">
                        <i class="fa fa-search"></i>
                    </button>
                    <Link to='/login'>Login/Register</Link>
                    </header>
                </div>
            );
        }
        else {
            return (
                <div>
                    <header className={showHeader}>
                    <img src={logo} id='logo'></img>
                    <Menu></Menu>
                    <input type="text" placeholder="Search.."></input>
                    <button type="submit">
                        <i class="fa fa-search"></i>
                    </button>
                    <Link to='/dashboard'><img src={profile} id='profile'></img></Link>
                    <Link to='' onClick={this.logout}>Logout</Link>
                    
                    </header>
                </div>
            );
        }

    }
}
export default withLogging(Header);